        <ul>
            <li v-for="item in qnalist" :key="item.num">
                <span>{{item.qnaNo}}</span>
                <span>{{item.qnaTitle}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
import Constant from "../js/Constant";

export default {
    name: "QnAList",
