<template>
  <div id="app">
    <qn-a-header></qn-a-header>
    <router-view></router-view>
  </div>
</template>

<script>
import Vue from "vue";
import VueRouter from "vue-router";
// 사용할 컴포넌트 import
import QnAList from "./components/QnAList.vue";
import QnAHeader from "./components/QnAHeader.vue";

Vue.use(VueRouter);

const router = new VueRouter({
  mode: "history",
  routes: [
    {
      path: "/",
      component: QnAList
    },
    {
      path: "/qna",
      component: QnAList
    }
  ]
});

export default {
  name: "app",
  components: {
    QnAHeader
  },
  created() {
    this.$router.push("/");
  },
  router,
  data() {
    return {
      qnalist: [],
      qna: {}
    };
  }
};
</script>


